
import { defineComponent, ref } from 'vue';
import { useStore } from 'vuex';
import { MutationTypes } from '@/store/mutation-types';
import ModalAccountOption from '@/components/balance/ModalAccountOption.vue';

export default defineComponent({
  components: {
    ModalAccountOption,
  },
  props: {
    allAccounts: {
      type: Array,
      required: true,
    },
    allAccountNames: {
      type: Array,
      required: true,
    },
    accountIdx: {
      type: Number,
      required: true,
    },
  },
  setup(props, { emit }) {
    const closeModal = () => {
      emit('update:is-open', false);
    };

    const store = useStore();

    const selectAccount = (accountIdx: Number) => {
      store.commit(MutationTypes.SET_CURRENT_ACCOUNT_IDX, accountIdx);

      emit('update:is-open', false);
    };

    const selAccount = ref(props.accountIdx);

    return {
      closeModal,
      selAccount,
      selectAccount,
    };
  },
});
