
import { defineComponent } from 'vue';

export default defineComponent({
  props: {
    ctype: {
      type: String,
      default: 'codehash',
    },
  },
  setup(props, { emit }) {
    const closeModal = () => {
      emit('update:is-open', false);
    };

    const forget = () => {
      emit('forget', true);
    };

    return {
      closeModal,
      forget,
    };
  },
});
