
import { defineComponent, computed, toRefs } from 'vue';
import { useStore } from 'vuex';
import { ActionTypes } from '@/store/action-types';
import IconBase from '@/components/icons/IconBase.vue';
import IconAccountSample from '@/components/icons/IconAccountSample.vue';
import IconChevronDown from '@/components/icons/IconChevronDown.vue';
import IconDocumentDuplicate from '@/components/icons/IconDocumentDuplicate.vue';

export default defineComponent({
  components: {
    IconBase,
    IconAccountSample,
    IconChevronDown,
    IconDocumentDuplicate,
  },
  props: {
    address: {
      type: String,
      required: true,
    },
    addressName: {
      type: String,
      required: true,
    },
  },
  setup(props, { emit }) {
    const openModal = () => {
      emit('update:is-open', true);
    };

    const { address } = toRefs(props);

    const shortenAddress = computed(() => {
      return address.value
        ? `${address.value.slice(0, 6)}${'.'.repeat(6)}${address.value.slice(
            -6
          )}`
        : '';
    });

    const store = useStore();

    const showAlert = () => {
      store.dispatch(ActionTypes.SHOW_ALERT_MSG, {
        msg: 'Copy address success!!',
        alertType: 'success',
      });
    };

    return {
      openModal,
      shortenAddress,
      showAlert,
    };
  },
  methods: {
    copyAddress() {
      var copyAddr = document.querySelector('#hiddenAddr') as HTMLInputElement;
      copyAddr.setAttribute('type', 'text');
      copyAddr.select();
      document.execCommand('copy');
      copyAddr.setAttribute('type', 'hidden');
      window.getSelection()?.removeAllRanges();

      this.showAlert();
    },
  },
});
