
import { defineComponent } from 'vue';

export default defineComponent({
  props: {
    connectionType: {
      type: String,
      default: 'connected',
      required: true,
    },
  },
  methods: {
    getDotClass(connectionType: string) {
      if (connectionType === 'connecting') {
        return 'orange';
      } else if (connectionType === 'connected') {
        return 'green';
      } else if (connectionType === 'offline') {
        return 'red';
      }
    },
  },
});
