
import { defineComponent } from 'vue';
import { useChainMetadata } from '@/hooks';
import IconBase from '@/components/icons/IconBase.vue';
import IconAccountSample from '@/components/icons/IconAccountSample.vue';
import FormatBalance from '@/components/balance/FormatBalance.vue';
// import IconTrendingDown from '@/components/icons/IconTrendingDown.vue';
// import IconTrendingUp from '@/components/icons/IconTrendingUp.vue';

export default defineComponent({
  components: {
    IconBase,
    IconAccountSample,
    // IconTrendingDown,
    // IconTrendingUp,
    FormatBalance,
  },
  props: {
    address: {
      type: String,
      required: true,
    },
  },
  setup(props, { emit }) {
    const openTransferModal = (): void => {
      emit('update:is-open-transfer', true);
    };

    const { defaultUnitToken } = useChainMetadata();

    return {
      openTransferModal,
      defaultUnitToken,
    };
  },
});
